@font-face {
  font-family: Avenir-Next-Thin;
  src: url(/fonts/avenir-next/avenir-next-thin.ttf) format("truetype"); }
@font-face {
  font-family: Avenir-Next-Regular;
  src: url(/fonts/avenir-next/avenir-next-regular.ttf) format("truetype"); }
@font-face {
  font-family: Avenir-Next-Medium;
  src: url(/fonts/avenir-next/avenir-next-medium.ttf) format("truetype"); }
@font-face {
  font-family: Avenir-Next-DemiBold;
  src: url(/fonts/avenir-next/avenir-next-demibold.ttf) format("truetype"); }
@font-face {
  font-family: Avenir-Next-Bold;
  src: url(/fonts/avenir-next/avenir-next-bold.ttf) format("truetype"); }
@font-face {
  font-family: Avenir-Next-Italic;
  src: url(/fonts/avenir-next/avenir-next-italic.ttf) format("truetype"); }
@font-face {
  font-family: Lato-Thin;
  src: url(/fonts/lato/Lato-Thin.ttf) format("truetype"); }
@font-face {
  font-family: Lato-Light;
  src: url(/fonts/lato/Lato-Light.ttf) format("truetype"); }
@font-face {
  font-family: Lato-Regular;
  src: url(/fonts/lato/Lato-Regular.ttf) format("truetype"); }
@font-face {
  font-family: Lato-Medium;
  src: url(/fonts/lato/Lato-Medium.ttf) format("truetype"); }
@font-face {
  font-family: Lato-Bold;
  src: url(/fonts/lato/Lato-Bold.ttf) format("truetype"); }
body {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body * {
  font-family: Avenir-Next-Regular; }

body .row {
  margin-left: 0;
  margin-right: 0; }

ul {
  list-style-type: none;
  padding-left: 0; }

a, a:hover {
  text-decoration: none; }

strong {
  font-family: Avenir-Next-Medium; }

.mdash {
  font-family: Lato; }



